.myProjects{
    ul{
        width: 80%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        list-style: none;
    }
}
.projects{
    &__item{
        box-sizing: border-box;
        width: 37%;
        background: rgb(101, 0, 184);
        color: white;
        h3{
            margin: 0.5em 0;
        }
        figure{
            position: relative;
            margin: 1em;
            padding-bottom: 1em;
            line-height: 170%;
            &::before{
                content: '';
                position: absolute;
                top: calc(0%);
                display: block;
                width: calc(100% + 2px);
                height: 100%;
                border-top: 1px solid white;
                border-bottom: 1px solid white;
                transition-duration: 0.2s;
                transform: scaleX(0);
            }
            &::after{
                content: '';
                position: absolute;
                width: 100%;
                height: calc(100% + 1px);
                bottom: calc(0% - 1px);
                display: block;
                width: 100%;
                border-left: 1px solid white;
                border-right: 1px solid white;
                transition-duration: 0.2s;
                transform: scaleY(0);
            }
            &:hover{
                &::before{
                    transform: scaleX(1);
                }
                &::after{
                    transform: scaleY(1);
                }
            }
        }
        
        img{
            width: 26vw;
            height: 17vw;
            margin: 2em 0em 0em 0em;
        }
        a{
            position: sticky;
            z-index: 50;
            color: white;
            text-decoration: none;
        }
        a:hover{
            text-decoration: underline;
        }
    }
}


@media screen and (max-width: 992px) {
    .myProjects{
        ul{
            flex-direction: column-reverse;
            align-items: center;
        }
    }
    .projects{
        &__item{
            width: 80%;
            margin: 1em 1.5em 0em 0em;
            img{
                width: 50vw;
                height: 35vw;
            }
        }
    }
}